@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP&display=swap);
body {
    /* border: 1px solid black; */
    max-width: 100%;
    display: flex;
    justify-content: center;
    align-content: center;
    color: rgb(229, 229, 229);
    font-family: "Noto Sans JP", sans-serif;
}

.App {
    border: 2px double rgb(229, 229, 229);
    background-color: rgb(20, 33, 61);
    min-width: 80vw;
    max-width: 80vw;
    display: flex;
    /* padding: 5px; */
}

.MainWindow {
    flex-basis: 70%;
    flex-grow: 8;
    flex-shrink: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    /* border-right: 10px solid rgb(20, 33, 61); */
}

.SideWindow {
    flex-basis: 30%;
    padding: 10px;
    flex-grow: 2;
    /* flex-shrink: 1; */
}
.AnswerButtonContainer {
    min-width: 100%;
    min-height: 100%;
}

.AnsButton {
    min-width: 100%;
    min-height: 100%;
    font-size: 1em;
    letter-spacing: 0.1em;
    font-weight: 600;
    padding: 10px 0px 10px 0px;
    background-color: white;
    box-shadow: 0px 0px 10px 10px aliceblue inset;
    text-shadow: 1px 1px 2px rgba(161, 90, 90, 0.25);
    border: 1px solid aliceblue;
    transition: letter-spacing 0.2s;
    outline: none;
}

.AnsButton:hover:enabled {
    color: rgb(28, 46, 83);
    background-color: white;
    box-shadow: 0px 0px 10px 5px aliceblue inset;
    padding: none;
    letter-spacing: 0.12em;
}

.AnsButton:active {
    color: rgb(252, 163, 17);
}

._DebugCorrect {
    font-style: italic;
}

.QuestionDisplayContainer {
    display: flex;
    flex-direction: column;
}

.QuestionTextDiv {
    border: 1px solid grey;
    background-color: black;
    padding: 15px;
    text-align: justify;
    line-height: 1.4em;
    letter-spacing: 0.08em;
    text-shadow: 0px 0px 8px rgb(252, 163, 17);
    box-shadow: 0px 0px 30px 5px rgba(252, 163, 17, 0.2) inset;
}

.QuestionText::first-letter {
    font-size: 200%;
    font-style: italic;
    margin-right: 3px;
    margin-left: -3px;
}

.AnswersGrid {
    display: grid;
    grid-template-rows: auto auto;
    grid-template-columns: auto auto;
    background-color: white;
    grid-gap: 2px;
    gap: 2px;
    margin: 0px;
}

.SidePanelContainer {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.Disabled {
  opacity: 50%;
}

.PrizeAmountsDiv {
  background-color: black;
  border: 2px double rgba(255, 255, 255, 0.6);
  margin: 5px 5px 10px 5px;
  box-shadow: 0px 0px 30px 5px rgba(252, 163, 17, 0.2) inset;
  padding: 10px;
}

.PrizeAmountsHeader {
  margin: 0px;
  font-size: 1em;
  padding-bottom: 4px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.8);
  font-weight: 600;
  text-shadow: 1px 1px 3px rgba(252, 163, 17, 0.7);
  letter-spacing: 0.2em;
}

.PrizeAmountsList {
  list-style-type: none;
  font-weight: normal;
  text-shadow: 1px 0px 2px rgba(255, 255, 255, 0.8);
  border-bottom: 1px solid rgba(255, 255, 255, 0.6);
  padding-bottom: 20px;
  margin-bottom: 10px;
}

.SafeAmount {
  color: gold;
}

.CurrentValue {
  font-weight: bolder;
  color: green;
  text-shadow: none;
  letter-spacing: 0.1em;
  font-size: 1.1em;
  border-left: 2px double green;
  padding-left: 5px;
}

.CurrentValueSpecial {
  font-weight: bolder;
  color: green;
  text-shadow: 0px 0px 5px rgb(252, 163, 17);
  border-left: 2px double rgb(252, 163, 17);
  padding-left: 5px;
  letter-spacing: 0.1em;
  font-size: 1.1em;
}

.AmountReached {
  opacity: 40%;
}

.QuitContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}

.LeaveEarlyAmount {
  font-weight: 800;
  text-shadow: 0px 0px 5px rgb(252, 163, 17);
}

.LeaveEarlyText {
  padding: 0px 0px 10px 0px;
  margin: 0px;
  font-weight: 400;
}

.LeaveEarlyButton {
  width: 100%;
  padding: 0px;
  font-size: 0.8em;
  letter-spacing: 0.1em;
  font-weight: 600;
  padding: 4px 4px 4px 4px;
  background-color: white;
  box-shadow: 0px 0px 10px 10px aliceblue inset;
  text-shadow: 1px 1px 2px rgba(161, 90, 90, 0.25);
  border: 1px solid aliceblue;
  outline: none;
}

.LeaveEarlyButton:hover:enabled {
  text-decoration: underline;
  cursor: pointer;
  color: rgb(20, 33, 61);
}

@media only screen and (max-width: 600px) {
  .PrizeAmountsList {
    margin-left: -30px;
    border: none;
    padding-bottom: 5px;
  }

  .LeaveEarlyText {
    border-top: 1px solid rgba(255, 255, 255, 0.6);
    padding-top: 10px;
  }
}

.LifelineDisplayContainer {
    display: flex;
    flex-direction: column;
    border: 2px double rgba(255,255,255,0.7);
    padding: 10px;
    margin: 0px 5px 5px 5px;
    background-color: black;
    box-shadow: 0px 0px 30px 5px rgba(252, 163, 17, 0.2) inset;

}

.LifelineDisplayHeaderContainer {
    padding-bottom: 15px;
}
.LifelineDisplayHeader {
    margin: 0px;
    font-size: 1.0em;
    padding-bottom: 4px;
    border-bottom: 1px solid rgba(255,255,255,0.8);
    font-weight: 600;
    text-shadow: 1px 1px 3px rgba(252, 163, 17, 0.7);
    letter-spacing: 0.2em;
}

.LifelineButtonsContainer {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
}

.LifelineButton {
    margin: 2px;
    width: 64px;
    height: 64px;
}

@media only screen and (max-width: 720px) {

    .LifelineButtonsContainer {
        justify-content: space-between;
    }
    .LifelineButton {
        width: 48px;
        height: 48px;
    }
}

@media only screen and (max-width: 600px) {

    .LifelineButtonsContainer {
        justify-content: space-between;
    }
    
    .LifelineButton {
        width: 32px;
        height: 32px;
    }
}


.LifelineButton:hover:enabled {
    cursor: pointer;
}

.LifelineButton:disabled {
    opacity: 50%;
}

.PortraitWindowContainer {
    height: 100%;
    border-right: 2px double rgba(255,255,255,0.5);
    display: flex;
    justify-content: flex-start;
    align-items: flex-end;
    padding: 10px;
}

.PortraitWindowContent {
    width: 100%;
    height: 100%;
    margin: 0px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;

}

.PortraitWindowContent > img {
    max-width: 200px;
    margin: 0px;
    justify-self: center;
    align-self: center;
}

.PortraitWindowText {
    margin: 0px;
    min-width: 80%;
    padding: 4px 4px 4px 15px;
    font-size: 0.9em;
    flex-basis: 10%;
    color: white;
    background-color: black;
    border: 2px double rgb(229, 229, 229);
} 

.PapSuggestedAnswer {
    text-shadow: 0px 0px 3px rgb(252, 163, 17);
    font-weight: bold;
    font-style: italic;
    opacity: 1;
    -webkit-animation: fadeIn 1s;
            animation: fadeIn 1s;
}

.PrevCorrectAnswer {
    font-weight: bold;
    font-style: italic;
    text-shadow: 0px 0px 3px rgb(252, 163, 17);
}

.CorrectAnswerIcon {
    position: absolute;
    margin-left: auto;
    margin-top: auto;
    opacity: 0;
    -webkit-animation: fadeInOut 1s;
            animation: fadeInOut 1s;
}

.BarChart {
    align-self: flex-start;
    position: absolute;
    margin-left: auto;
}

@-webkit-keyframes fadeInOut {
  0%   { opacity:0; }
  50%  { opacity:1; }
  100% { opacity:0; }
}

@keyframes fadeInOut {
  0%   { opacity:0; }
  50%  { opacity:1; }
  100% { opacity:0; }
}

@-webkit-keyframes fadeIn {
    0% {opacity: 0;}
    100% {opacity: 1;}
}

@keyframes fadeIn {
    0% {opacity: 0;}
    100% {opacity: 1;}
}
.BarChart {
    -webkit-animation: fadeIn 1s;
            animation: fadeIn 1s;
}

@-webkit-keyframes fadeIn {
    0% {opacity: 0;}
    100% {opacity: 1;}
}

@keyframes fadeIn {
    0% {opacity: 0;}
    100% {opacity: 1;}
}
.GameOverScreenContainer {
  width: 100%;
  height: 100%;
  border-right: 2px double rgba(255, 255, 255, 0.5);
  text-shadow: 1px 1px 10px rgba(0, 0, 0, 0.5);
  text-align: center;
  color: rgb(238, 238, 238);
  padding: 0px;
}

.WinScreen {
  width: 100%;
  height: 100%;
  margin: 0px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  background-image: linear-gradient(to left top, #008793, #00bf72, #a8eb12);
}

.WinScreenContent {
  height: 80%;
  max-width: 80%;
  display: flex;
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 5%;
  padding: 0px 5vw 0px 5vw;
  background-color: rgba(255, 255, 255, 0.1);
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.Congratulations {
  font-size: 3em;
  padding-bottom: 5px;
  letter-spacing: 0.12em;
  opacity: 1;
  -webkit-animation: fadeIn 2s;
          animation: fadeIn 2s;
}

.CongratulationsSubtitle {
  font-size: 1.8em;
  letter-spacing: 0.07em;
  font-style: italic;
  opacity: 1;
  -webkit-animation: wait 1.5s, fadeIn 2s;
          animation: wait 1.5s, fadeIn 2s;
  -webkit-animation-delay: 0s, 1.5s;
          animation-delay: 0s, 1.5s;
}

.OneMillion {
  font-size: 4em;
  font-weight: 800;
  color: rgb(41, 190, 41);
  letter-spacing: 0.1em;
  text-shadow: 0px 0px 8px rgb(255, 255, 255);
  opacity: 1;
  -webkit-animation: wait 3s, fadeInOut 2s infinite;
          animation: wait 3s, fadeInOut 2s infinite;
  -webkit-animation-delay: 0s, 3s;
          animation-delay: 0s, 3s;
}

.RetireScreen {
  width: 100%;
  height: 100%;
  border: 1px solid rgba(0, 0, 0, 0);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-image: linear-gradient(
    to right top,
    #cebfe5,
    #c3bbe3,
    #b8b7e0,
    #acb3dd,
    #a0afda
  );
  opacity: 1;
  -webkit-animation: fadeIn 1s;
          animation: fadeIn 1s;
}

.RetireScreenContent {
  width: 80%;
  height: 65%;
  border: 1px solid rgba(255, 255, 255, 0.5);
  background-color: rgba(255, 255, 255, 0.2);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.RetireHeader {
  font-size: 2em;
  letter-spacing: 0.1em;
  opacity: 1;
  -webkit-animation: wait 1s, fadeIn 1s;
          animation: wait 1s, fadeIn 1s;
  -webkit-animation-delay: 0s, 1s;
          animation-delay: 0s, 1s;
}

.Fade {
  -webkit-animation: wait 2s, fadeIn 1s;
          animation: wait 2s, fadeIn 1s;
  -webkit-animation-delay: 0s, 2s;
          animation-delay: 0s, 2s;
}

.RetireSubtitle {
  font-size: 1.2em;
  letter-spacing: 0.05em;
  -webkit-animation: wait 2s, fadeIn 1s;
          animation: wait 2s, fadeIn 1s;
  -webkit-animation-delay: 0s, 2s;
          animation-delay: 0s, 2s;
}

.RetireAmount {
  font-size: 3em;
  -webkit-animation: wait 3s, fadeIn 1s, wordSpread 1s;
          animation: wait 3s, fadeIn 1s, wordSpread 1s;
  -webkit-animation-delay: 0s, 3s, 3s;
          animation-delay: 0s, 3s, 3s;
}

.LoseScreen {
  border: 1px solid rgba(0, 0, 0, 0);
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-image: linear-gradient(
    to right top,
    #f28484,
    #c08354,
    #877e45,
    #55734d,
    #346155
  );
}

.LoseScreenContent {
  width: 65%;
  height: 80%;
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 1px solid rgba(255, 255, 255, 0.5);
  background-color: rgba(255, 255, 255, 0.2);
  -webkit-animation: fadeIn 1s;
          animation: fadeIn 1s;
}

.LoseScreenHeader {
  font-size: 2em;
}

.LoseScreenSubtitle {
  font-size: 1.2em;
  margin-left: 40px;
  margin-right: 40px;
}

.Gold {
    color: rgb(252, 190, 90);
}

.SafetyNetAmount {
  font-size: 3em;
  -webkit-animation: wait 1s, fadeIn 1s, wordSpread 1s;
          animation: wait 1s, fadeIn 1s, wordSpread 1s;
  -webkit-animation-delay: 0s, 1s, 1s;
          animation-delay: 0s, 1s, 1s;
}

.CorrectAnswer {
    font-weight: 600;
    letter-spacing: 0.05em;
}

.ReloadButton {
  width: 80%;
  padding: 5px;
  border-radius: 33%;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(0, 0, 0, 0.1);
  outline: none;
  font-weight: 600;
  letter-spacing: 0.05em;
}

.ReloadButton:hover {
  cursor: pointer;
  color: rgba(0, 0, 0, 0.7);
}

@media only screen and (max-width: 1200px) {
  .Congratulations {
    font-size: 2.5em;
    letter-spacing: 0.05em;
  }

  .CongratulationsSubtitle {
    font-size: 1.3em;
  }

  .OneMillion {
    font-size: 2.2em;
  }

  .LoseScreenHeader {
    font-size: 1.5em;
  }

}

@media only screen and (max-width: 900px) {
  .Congratulations {
    font-size: 1.7em;
    letter-spacing: 0.05em;
  }
  .RetireAmount {
    font-size: 2em;
  }

  .LoseScreenSubtitle {
    margin-left: 20px;
    margin-right: 20px;
    font-size: 1em;
  }

  .SafetyNetAmount {
    font-size: 2em;
  }
}

@media only screen and (max-width: 650px) {
  .Congratulations {
    font-size: 1.2em;
    letter-spacing: 0.05em;
  }

  .CongratulationsSubtitle {
    font-size: 1em;
  }

  .OneMillion {
    font-size: 1.5em;
  }
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-webkit-keyframes wait {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}

@keyframes wait {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}

@-webkit-keyframes fadeInOut {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes fadeInOut {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@-webkit-keyframes wordSpread {
  0% {
    letter-spacing: -1em;
  }
  100% {
    letter-spacing: 0em;
  }
}

@keyframes wordSpread {
  0% {
    letter-spacing: -1em;
  }
  100% {
    letter-spacing: 0em;
  }
}

